.Navigation
    width: 100%
    height: 65px
    position: fixed
    z-index: 1
    top: 0 
    background-color: #FAFAFA
    border-bottom: 1px solid #DEDEDE
    &-Hamburger
        display: none
    &-Container
        display: flex
        align-items: center
        justify-content: space-between
        width: 1136px
        height: 65px
        margin: 0 auto
    &-URLs
        width: 620px
        display: flex
        justify-content: space-between
        align-items: center
        height: 100%
    &-Buttons
        width: 280px
        display: flex
        justify-content: space-between
        align-items: center
        height: 100%
        position: relative
    &-Logo
        height: 34px
    &-Links
        list-style: none
        padding: 0
        display: flex
        justify-content: space-between
        align-items: center
        width: 520px
        height: 100%
        transition: 1s ease all
    &-Text
        text-decoration: none
        font-weight: normal
        font-size: 14px
        line-height: 64px
        color: #121212
    &-Link
        width: 130px
        height: 100%
        text-align: center
        position: relative
    &-Panel
        width: 80px
        position: relative
        font-weight: normal
        font-size: 15px
        line-height: 17px
        display: flex
        align-items: center
        justify-content: center
        text-align: center
        letter-spacing: 0.01em
        color: #121212
        height: 100%
    &-Link, &-Panel
        &::before
            content: ''
            display: block
            position: absolute
            width: 0%
            height: 2px
            background-color: #121212
            bottom: 0
            left: 50%
            transform: translateX(-50%)
            transition: 0.6s ease all
        &:hover
            &::before
                width: 100%
    &-Searchbar
        position: absolute
        left: -60%
        overflow: hidden
        &_line
            position: absolute
            height: 100%
            width: 100%
            pointer-events: none
            transition: .3s ease all
            &::before
                content: ''
                display: block
                position: absolute
                width: 100%
                height: 1px
                background-color: #121212
                bottom: 0
                left: 0
                transform: translateY(-50%)
        &_input
            border: none
            background: 0 0
            outline: 0
            height: 100%
            width: 100%
            padding: 0 10px
            background-color: #FAFAFA
.line 
  fill: none
  stroke: black
  stroke-width: 6
  transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1)
.line1 
  stroke-dasharray: 60 207
  stroke-width: 6
.line2 
  stroke-dasharray: 60 60
  stroke-width: 6
.line3 
  stroke-dasharray: 60 207
  stroke-width: 6
.opened .line1 
  stroke-dasharray: 90 207
  stroke-dashoffset: -134
  stroke-width: 6
.opened .line2 
  stroke-dasharray: 1 60
  stroke-dashoffset: -30
  stroke-width: 6
.opened .line3 
  stroke-dasharray: 90 207
  stroke-dashoffset: -134
  stroke-width: 6